<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <span>成品{{cardTitle}}</span>
      </div>
      <el-form
        class=""
        style="padding: 0 60px 0 20px; width: 650px"
        ref="formRef"
        :model="form"
        :rules="formRules"
        label-width="100px"
      >
        <el-form-item prop="count" :label="cardTitle+'数量'">
          <el-input
            ref="firstInput"
            v-model.number="form.count"
            @input="inputChange"
          ></el-input>
        </el-form-item>
        <div
          v-for="(item, index) in form.response_datas"
          :key="index + ' onlyItem'"
        >
          <el-form-item
            :prop="'response_datas.' + index + '.pack_id'"
            :rules="[]"
            label="包装编码"
          >
            <el-input
              :ref="'finishedProductCode' + index"
              v-model="item.pack_id"
              @input="
                nextFocus(
                  'finishedProductCode' + (index + 1),
                  index,
                  item.pack_id
                )
              "
            ></el-input>
          </el-form-item>
        </div>
      </el-form>

      <div>
        <el-button @click="resetForm">重 置</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "MissStation",

  props: {
    formStatus: {
      type: String,
      default: "",
    },
    cardTitle: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      timeout: "",
      // 产品过站弹出层
      form: {
        count: "",
        status: "",
        response_datas: [],
      },
      formBase: {
        count: "",
        status: "",
        response_datas: [],
      },
      formRules: {
        count: [{ required: true, message: "请输入数量", trigger: "blur" },
         { type: 'number', message: '数量必须为数值', trigger: "blur"  },],
      },
    };
  },
  computed: {},
  methods: {
    inputChange() {
      this.form.response_datas = [];
      for (let index = 0; index < this.form.count; index++) {
        this.form.response_datas.push({
          pack_id: "",
        });
      }
    },
    nextFocus(nextRef, index, matter_code) {
      if (this.timeout) {
        clearTimeout(this.timeout);
      }
      this.timeout = setTimeout(() => {
        if (index < this.form.response_datas.length - 1) {
          this.$nextTick(() => {
            this.$refs[nextRef][0].focus();
          });
        } else {
          console.log(5789);
        }
      }, 500);
    },

    // 产品过站 新增
    getLogisticstockInStockOut(params) {
      baseManagement.getLogisticstockInStockOut(params).then((res) => {
        console.log("产品过站 新增", res);
        if (res.code == 0) {
          this.form = JSON.parse(JSON.stringify(this.formBase));
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 重置按钮
    resetForm() {
      this.form = JSON.parse(JSON.stringify(this.formBase));
    },

    // 确认按钮
    dialogSave() {
      console.log("this.form", this.form);

      this.$refs.formRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          console.log("产品过站", this.form);
          let params = JSON.parse(JSON.stringify(this.form));
          params.status=this.formStatus
          params.response_datas = JSON.stringify(this.form.response_datas);
          console.log("paramsparams", params);
          this.getLogisticstockInStockOut(params);
        }
      });
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.firstInput.focus();
    });
  },
};
</script>

